import React,{Component} from 'react';
import {
    View,
    Text,
    Image,
    TextInput,
    TouchableOpacity
} from 'react-native';
import styles from './Style';
import FooterButton from './FooterButton';
import firebase from 'react-native-firebase';
import Toast from 'react-native-easy-toast';

export default class LoginScreen extends Component{
    constructor(props){
        super(props);
        this.state = {
            idText: '',
            pwText: '',
            id: '',
            pw: ''
        }
    }

    handleLogin = () => {
        const { id, pw } = this.state;
        firebase
            .auth()
            .signInWithEmailAndPassword(id, pw)
            .then(() => this.props.navigation.navigate('Main'))
            .catch(() => this.refs.toast.show('  .   !',500))
    }

    render() {
        return(
            <View style={styles.container}>
                <Image 
                    source={require('./icon.png')}
                    style={styles.icon} 
                />
                <Text style={styles.title}>For Marketing</Text>
                <TextInput 
                    style={styles.textInputButton}
                    onChangeText={(id) => this.setState({id})}
                    placeholder={this.state.idText}
                    autoCorrect={false}
                />
                <TextInput 
                    style={styles.textInputButton}
                    onChangeText={(pw) => this.setState({pw})}
                    placeholder={this.state.pwText}
                    autoCorrect={false}
                    secureTextEntry={true}
                />
                <FooterButton 
                    buttonText=""
                    onPress={this.handleLogin}
                />
                <Text style={styles.noAccountText}> ?</Text>
                <TouchableOpacity
                    onPress={() => this.props.navigation.navigate('SignUp')}>
                    <Text style={styles.makeAccountText}> </Text>
                </TouchableOpacity>
                <Toast ref="toast" />
            </View>
        )
    }
}